<template>
  <div class="modal-login">
    <div class="pt-4 pb-4 text-center">
      <h1 class="font-parisienne color-point">Publessing</h1>
      <h3 class="font-coding">Login with email</h3>
    </div>
    <div class="form mt-2">
      <div class="mb-4">
        <label for="email">Email</label>
        <input type="email" class="form-control" id="email" v-model="state.account.email" placeholder="name@publessing.io" autocomplete="off" @keypress.enter="state.mode === 'login' ? login() : reset()" :disabled="!state.loaded" ref="emailRef" />
      </div>
      <div class="mb-4" v-if="state.mode === 'login'">
        <label for="password">Password</label>
        <input type="password" class="form-control" id="password" v-model="state.account.password" @keypress.enter="login()" :disabled="!state.loaded" />
      </div>
      <div class="labels mb-4" v-if="state.mode === 'login'">
        <div class="form-check">
          <label class="foat-left form-check-label" for="remember">
            <input type="checkbox" class="form-check-input" id="remember" v-model="state.account.remember" value="true" @keypress.enter="login()" :disabled="!state.loaded" />
            <span>Remember me</span>
          </label>
          <label class="reset float-right" @click="setModeReset()">Forgot password?</label>
        </div>
      </div>
      <template  v-if="state.mode === 'login'">
      <button class="btn-block btn-point no-radius" type="button" @click="login()" :disabled="!state.loaded">Login</button>
      <button class="btn-block btn-point-line no-radius" type="button" @click="callback('join')" :disabled="!state.loaded">Create account</button>
      </template>
      <button class="btn-block btn-point no-radius" type="button" @click="reset()" :disabled="!state.loaded" v-else>{{ `Send${state.loaded ? "" : "ing..."}` }}</button>
    </div>
  </div>
</template>
<script>
import { onMounted, reactive, ref } from "vue";
import httpLib from "../libs/httpLib";
import commLib from "../libs/commonLib";

export default {
  props: {
    callback: Function,
  },
  setup(props) {
    const state = reactive({
      mode: "login",
      loaded: true,
      account: {
        email: "",
        password: "",
        remember: false,
      },
    });

    const emailRef = ref(null);

    const setModeReset = () => {
      state.mode = "reset";
      document.getElementById("email").focus();
    };

    const reset = () => {
      if (!state.loaded) {
        return;
      } else if (!state.account.email || !state.account.email.trim()) {
        document.getElementById("email").focus();
        return commLib.message.show("warning", "이메일을 입력해주세요.");
      }

      state.loaded = false;

      httpLib
        .post("/api/account/reset", state.account)
        .then(() => {
          state.loaded = true;
          commLib.message.show("success", "입력하신 이메일로 패스워드 초기화 URL을 발송하였습니다.");
          props.callback("close-modal");
        })
        .catch((err) => {
          state.loaded = true;

          switch (err.response?.status) {
            case 404:
              commLib.message.show("warning", "해당 계정이 존재하지 않습니다. 정보를 다시 확인해주세요.");
              break;

            case 422:
              commLib.message.show("warning", "이메일 주소가 올바르지 않습니다.");
              break;

            case 429:
              commLib.message.show("warning", "잠시 후 다시 시도해주세요.");
              break;

            default:
              commLib.message.show("danger", "오류가 있습니다.");
              break;
          }
        });
    };

    const login = () => {
      if (!state.loaded) {
        return;
      } else if (!state.account.email || !state.account.email.trim()) {
        document.getElementById("email").focus();
        return commLib.message.show("warning", "이메일을 입력해주세요.");
      } else if (!state.account.password || !state.account.password.trim()) {
        document.getElementById("password").focus();
        return commLib.message.show("warning", "패스워드를 입력해주세요.");
      }

      state.loaded = false;

      httpLib
        .post("/api/account/login", state.account)
        .then(() => {
          state.loaded = true;
          commLib.message.show("success", "로그인하였습니다.");
          props.callback("set-account");
          props.callback("close-modal");
          props.callback("go");
        })
        .catch((err) => {
          state.loaded = true;

          switch (err.response?.status) {
            case 404:
              commLib.message.show("warning", "해당 계정이 존재하지 않습니다. 정보를 다시 확인해주세요.");
              break;

            case 422:
              commLib.message.show("warning", "이메일 주소가 올바르지 않습니다.");
              break;

            case 429:
              commLib.message.show("warning", "잠시 후 로그인해주세요.");
              break;

            default:
              commLib.message.show("danger", "오류가 있습니다.");
              break;
          }
        });
    };

    onMounted(() => {
      setTimeout(() => {
        emailRef.value.focus();
      }, 250);
    });

    return {
      state,
      emailRef,
      login,
      reset,
      setModeReset,
    };
  },
};
</script>
<style lang="scss" scoped>
.modal-login {
  padding: 25px;

  h1 {
    font-weight: bold;
    font-size: 50px;
  }

  h3 {
    margin-top: 25px;
  }

  input:not([type="checkbox"]) {
    height: 50px;
    border-radius: 0;

    &:focus {
      outline: 0;
      box-shadow: none;
      border-color: #146ece;
    }
  }

  .labels {
    label {
      cursor: pointer;

      input[type="checkbox"] {
        margin-top: 0.4rem;
      }

      &.reset:hover {
        text-decoration: underline;
      }
    }
  }

  button {
    font-size: 16px;
    height: 50px;

    &[disabled] {
      opacity: 0.5;
    }
  }
}
</style>
