import { createStore } from "vuex";

export default createStore({
    state: {
        account: {
            mid: 0,
            email: "",
            server: "",
            resource: 0,
            login: false,
        },
        page: {
            name: null,
            seq: 1,
            maxSeq: 1,
            nid: "root",
        },
        refs: {
            console: null,
            webview: null
        },
        toggle: {
            tag: true,
            mobile: false
        },
        component: {
            categories: []
        }
    },
    mutations: {
        setRefs(state, refs) {
            state.refs = refs;
        },
        setPage(state, page) {
            state.page = page;
        },
        setToggle(state, payload) {
            if (payload.tag !== undefined) {
                state.toggle.tag = payload.tag;
            }

            if (payload.mobile !== undefined) {
                state.toggle.mobile = payload.mobile;
            }
        },
        setComponent(state, payload) {
            if (payload.categories !== undefined) {
                state.component.categories = payload.categories;
            }
        }
    }
})