<template>
  <div class="component-home" :browser="browser" :console="visible ? 'visible' : 'hidden'">
    <nav>
      <a href="/" class="brand font-parisienne">P</a>
      <ul>
        <li v-for="(n, idx) in state.navs" :class="{ active: n.active }" :key="idx" v-show="n.visible">
          <router-link :to="'/' + n.name">{{ n.name.substring(0, 1).toUpperCase() + n.name.substring(1) }}</router-link>
        </li>
      </ul>
      <button class="btn" @click="callback('toggle-console')">
        <span v-if="visible">CLOSE </span>
        <span v-else>OPEN </span>
        <span>CONSOLE</span>
      </button>
    </nav>
    <article>
      <router-view :visible="visible" :callback="callback" />
    </article>
    <footer>
      <div class="wrapper">
        <div class="copyright">&copy; 2021. Publessing. All rights reserved.</div>
        <div class="email">africalibrary21@gmail.com</div>
      </div>
    </footer>
    <button class="climb btn color-point border-point" ref="climbBtnRef" @click="climb()">TOP</button>
  </div>
</template>
<script>
import { onMounted, reactive, ref } from "@vue/runtime-core";
import commLib from "../libs/commonLib";
import $ from "jquery";

export default {
  props: {
    visible: Boolean,
    callback: Function,
  },
  setup() {
    const state = reactive({
      navs: [
        { name: "intro", active: false, visible: true },
        { name: "learn", active: false, visible: true },
        { name: "license", active: false, visible: true },
        { name: "contact", active: false, visible: true },
      ],
    });

    let timer = null;
    const climbBtnRef = ref(null);
    const browser = commLib.getBrowser();

    const climb = () => {
      $("html,body").animate({
        scrollTop: 0,
      });
    };

    const check = () => {
      if (window.scrollY > 300) {
        $(climbBtnRef.value).fadeIn();
      } else {
        $(climbBtnRef.value).fadeOut();
      }
    };

    const activate = () => {
      let name = "";

      if (location.pathname && location.pathname.length > 1) {
        const titleArr = document.title.split(" - ");

        name = location.pathname.substring(1);
        titleArr[0] = name[0].toUpperCase() + name.substring(1);
        document.title = titleArr.join(" - ");
      }

      state.navs.forEach((n) => {
        n.active = n.name === name;
      });
    };

    window.onscroll = () => {
      clearTimeout(timer);

      timer = setTimeout(() => {
        check();
      });
    };

    onMounted(() => {
      check();
    });

    return { state, climbBtnRef, browser, climb, activate };
  },
  watch: {
    $route() {
      $(window).scrollTop(0);
      this.activate();
    },
  },
};
</script>
<style lang="scss">
.component-home {
  position: relative;

  .video {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    border: 1px solid #ddd;

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .wrapper {
    width: 1140px;
    margin: 0 auto;
    max-width: 100%;
    padding: 0 15px;
  }

  > nav {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #ddd;
    background: #f7f7f7;
    transition: background-color 0.25s;
    padding-left: 70px;
    padding-right: 170px;
    z-index: 1;

    .brand {
      position: absolute;
      color: #000;
      top: 0;
      left: 0;
      font-size: 20px;
      cursor: pointer;
      font-weight: bold;
      text-decoration: none;
      border-right: 1px solid #ddd;
      height: 100%;
      width: 60px;
      text-align: center;
      line-height: 64px;
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      li {
        display: inline-block;
        margin: 0 5px;

        > a {
          color: inherit;
          padding: 19px 10px;
          cursor: pointer;
          display: inline-block;
          font-size: 14px;
          text-decoration: none;
        }

        &.active {
          font-weight: bold;
        }
      }
    }

    button {
      position: absolute;
      border-radius: 0;
      top: 6.5px;
      right: 7px;
      box-shadow: none;
      border: 1px solid #146ece;
      background: inherit;
      color: #146ece;
      padding: 11.5px 0;
      width: 150px;
      font-size: 14px;

      &:hover {
        background: #146ece;
        color: #fff;
      }
    }

    &.white {
      background: #fff;
    }
  }

  .parallax {
    padding-top: 100px;
    padding-bottom: 100px;
    color: #fff;
    text-align: center;
    position: relative;

    h1 {
      font-size: 42px;
      letter-spacing: 1px;
      margin-bottom: 20px;
      font-weight: bold;
    }

    h2 {
      font-size: 21px;
    }

    &:before {
      content: "";
      display: block;
      background-size: cover;
      background-position: center;
      background-attachment: fixed;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      filter: brightness(0.3);
      z-index: -1;
    }
  }

  > article {
    .part {
      padding-bottom: 25px;

      .sm-title {
        font-size: 18px;
        font-weight: 600;
        padding-bottom: 15px;
        margin-top: 30px;
        border-bottom: 1px solid #ccc;
        margin-bottom: 15px;
      }
    }
  }

  > footer {
    border-top: 1px solid #ddd;
    padding: 35px 0;
    background: #f7f7f7;

    > .wrapper:after {
      content: "";
      clear: both;
      display: table;
    }

    .copyright {
      float: left;
    }

    .email {
      float: right;
    }
  }

  > .climb {
    position: fixed;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 0;
    right: 15px;
    bottom: 15px;
    padding: 0;
    width: 66px;
    height: 66px;
    line-height: 66px;
    text-align: center;
    border: 1px solid;
    box-shadow: none;
    display: none;
    z-index: 10;

    &.on {
      opacity: 1;
    }
  }

  &[browser="safari"] {
    > article > div.parallax:before {
      background-attachment: scroll;
    }
  }

  @media (min-height: 2000px) {
    > nav ul li.active {
      font-weight: normal;
    }
  }

  @media (max-width: 1199px) {
    > nav button {
      display: none;
    }
  }

  @media screen and (orientation: landscape) {
    &[device="mobile"] > header {
      margin-top: 0;
    }
  }
}
</style>
