<template>
  <div class="modal-code">
    <div class="top">
      <span class="name">{{ data.component.category }} {{ data.component.num }}</span>
      <span class="desc">{{ data.component.desc }}</span>
    </div>
    <div class="area html">
      <pre><code class="language-html">{{ state.code.html }}</code></pre>
      <button class="btn btn-point" @click="commLib.copy(state.code.html)">Copy</button>
    </div>
    <div class="area css">
      <pre><code class="language-css">{{ state.code.css }}</code></pre>
      <button class="btn btn-point" @click="commLib.copy(state.code.css)">Copy</button>
    </div>
    <div class="area javascript">
      <pre><code class="language-javascript">{{ state.code.javascript }}</code></pre>
      <button class="btn btn-point" @click="commLib.copy(state.code.javascript)">Copy</button>
    </div>
  </div>
</template>
<script>
import httpLib from "../libs/httpLib";
import commLib from "../libs/commonLib";
import { nextTick, reactive } from "@vue/runtime-core";
import hljs from "highlight.js/lib/core";
import javascript from "highlight.js/lib/languages/javascript";
import css from "highlight.js/lib/languages/css";
import html from "highlight.js/lib/languages/xml";
import "highlight.js/styles/vs2015.css";

export default {
  props: {
    data: Object,
  },
  setup(props) {
    const state = reactive({
      code: {
        html: "",
        css: "",
        javascript: "",
      },
    });

    const args = {
      category: props.data.component.category,
      num: props.data.component.num,
      nid: null,
    };

    const hashmap = commLib.getHashmapFromCookie();

    if (hashmap[props.data.component.category]) {
      args.nid = props.data.component.category + (hashmap[props.data.component.category] + 1);
    } else {
      args.nid = props.data.component.category + "1";
    }

    httpLib.get("/api/components/node", args).then((res) => {
      state.code = res.data;

      hljs.registerLanguage("javascript", javascript);
      hljs.registerLanguage("css", css);
      hljs.registerLanguage("html", html);

      nextTick(() => {
        document.querySelectorAll("pre code").forEach((elem) => {
          hljs.highlightElement(elem);
        });
      });
    });

    return { state, commLib };
  },
};
</script>
<style lang="scss" scoped>
.modal-code {
  background: #1e1e1e;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  border: 1px solid #3c3c3c;

  .top {
    position: absolute;
    top: -60px;
    color: #fff;
    width: 100%;
    left: 0;
    font-size: 20px;
    padding: 15px 100px 15px 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    .name {
      font-size: 20px;
      display: inline-block;
      margin-right: 15px;
      font-weight: bold;
    }

    .desc {
      font-size: 14px;
    }
  }

  .area {
    width: 50%;
    float: left;
    position: relative;

    pre {
      height: 100%;
      overflow: auto;
      padding: 15px;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */

      code {
        white-space: pre;
        color: #fff;
      }

      &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
        background-color: #1e1e1e;
      }

      &::-webkit-scrollbar-thumb {
        background: #aaa;
      }
    }

    button {
      position: absolute;
      right: 15px;
      bottom: 15px;
      border-radius: 0;
      font-size: 13px;
    }

    &.html {
      height: 100%;
      border-right: 1px solid #3c3c3c;
    }

    &.css,
    &.javascript {
      height: 50%;
    }

    &.javascript {
      border-top: 1px solid #3c3c3c;
    }
  }
}
</style>
